$color-nav: #212d3a;
$color-social: #2e3f51;
$color-social-hover: #db4a39;
$color-white: #fff;
$color-card-desc: #2f3944;
$color-nav-text: #999999;
$color-resp-text: #505050;

//reset settings
ul, li, menu, a{
    margin: 0;
    padding: 0;
    list-style: none;
    text-decoration: none;
}

// general settings
body{
    margin: 0;
    padding: 0;
    color: #fff;
    box-sizing: border-box;
    font-family: Georgia, 'Times New Roman', Times, serif;
}
.container{
    max-width: 960px;
    margin: 0 auto;
}

// preloader settings
.preloader{
    position: fixed; 
    top: 0;
    bottom: 0; 
    left: 0; 
    right: 0; 
    display: flex; 
    justify-content: center; 
    align-items: center; 
    background-color: #2e3f51; 
    z-index: 99;
    &-cont{
        position: relative;
    }
    &-wrap{
        border: 15px solid $color-nav-text;
        border-radius: 50%;
        width: 100px;
        height: 100px;
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
        border-top: 15px solid $color-white;
        animation: preloader 2s infinite linear;
    }
    p{
        position: absolute;
        top: 42px;
        left: 30px;
    }
}



// social-nav settings
.social-nav{
    background-color: $color-nav;
    padding: 14px 0;
    position: relative;
    z-index: 20;
    .container{
        display: flex;
        justify-content: space-between;
    }
    .social{
        display: flex;
        &-item{
            background-color: $color-social;
            border-radius: 50%;
            height: 26px;
            width: 26px;
            text-align: center;
            align-items: center;
            line-height: 29px;
            filter: opacity(50%);
            transition: all ease-in-out 0.2s;
            &:not(:first-child){
                margin-left: 10px;
            }
            &:hover{
                background-color: $color-social-hover;
                filter: opacity(100%);
            }
        }
    }
    .search{
        form{
            position: relative;

            input[type=text]{
                background-color: #2e3f51;
                border: none;
                border-radius: 12px;
                height: 25px;
                width: 210px;
                color: $color-white;
                box-sizing: border-box;
                padding-left: 7px;
                &:focus{
                    outline: none;
                }
            }
            span{
                position: absolute;
                top: 4px;
                right: 5px;
                background-color: #212d3a;
                height: 18px;
                width: 18px;
                display: inline-block;
                cursor: pointer;
                border-radius: 50%;
                img{
                    position: relative;
                    left: 5px;
                    bottom: 1px;
                }
            }
        }
    }
}

//hav settings
nav{
    padding: 35px 0;
    color: $color-nav-text;

    .container{
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
    }
    .logo{
        display: flex;
        flex-direction: column;
        img{
            width: fit-content;
        }
        span{
            margin-top: 15px;
        }
    }
    menu{
        align-self: center;
        ul{
            display: flex;
            list-style: none;
            li{
                &:not(:first-child){
                    margin-left: 20px;
                }
                a{
                    text-decoration: none;
                    text-transform: uppercase;
                    color: $color-nav-text;
                    font-size: 12px;
                    font-weight: bold;
                    transform: translateY(0px);
                    transition: all ease-in-out 0.2s;
                    display: block;
                    &:hover{
                        color: $color-nav;
                        transform: translateY(-3px);
                    }
                }
            }
        }
    }

    //-----------------------------------------------------------------------------------------------burger menu
    .burger-menu{
        display: none;
        align-self: center;
        a{
            img{
                width: 25px;
            }
        }
        .burger-menu-hidden{
            padding: 15px;
            position: absolute;
            background-color: #eee;
            left: 0;
            right: 0;
            top: 0;
            transform: translateY(-1000px);
            transition: transform 1s ease-in-out;
            z-index: 11;
            ul{
                text-align: center;
                font-weight: 600;
                li:not(:last-child){
                    margin-bottom: 10px;
                }
            }
            span{
                position: absolute;
                right: 15px;
                top: 12px;
                cursor: pointer;
            }
        }
    }
}

//header cards
.header-cards{
    padding: 40px 0;
    overflow: hidden;
    .container{
        .cards-wrap{
            position: relative;
        }
        .header-cards-battons{
            position: absolute;
            left: 20px;
            top: 30px;
            z-index: 4;
            img{
                transition: all ease-in-out 0.1s;
            }
            & a:not(:first-child){
                margin-left: 20px;
            }
            & a:hover{
                img{
                    transform: scale(1.2);
                }
            }
        }
    }
    .header-cards-cover{
        display: flex;
        overflow: hidden;
        max-width: 960px;
    }
    background-color: $color-social;
    .header-cards-item{
        position: relative;
        top: 0;
        left: 0;
        min-width: 960px;
        .item-description{
            max-width: 31%;
            padding: 110px 30px 35px 20px;
            box-sizing: border-box;
            position: relative;
            z-index: 3;
            background-color: rgba(47, 57, 68, 0.5);
            max-height: 450px;
            h2{
                font-size: 24px;
                font-weight: 100;
                margin: 0;
            }
            p{
                margin: 26px 0 22px 0;
                line-height: 22px;
                max-height: 195px;
                overflow: hidden;
            }
            a{
                color: $color-white;
                text-decoration: none;
                display: block;
                background-color: $color-nav;
                padding: 5px 30px;
                width: fit-content;
                border-radius: 16px;
                &:hover{
                    color: $color-nav-text;
                }
            }
        }
        .item-img{
            position: absolute;
            top: 0;
            left: 0;
            max-width: 960px;
            max-height: 450px;
            overflow: hidden;
            img{
                width: 960px;
                height: 450px;
                object-fit: cover;
            }
        }
    }
}


//responce settings
.responce{
    .container{
        padding: 30px;
        box-sizing: border-box;
        position: relative;
    }
    color: $color-resp-text;
    .responce-battons{
        display: flex;
        justify-content: space-between;
        position: absolute;
        left: 30px;
        right: 30px;
        img{
            transition: all ease-in-out 0.1s;
        }
        & a:hover{
            img{
                transform: scale(1.2);
            }
        }
    }
    .responce-content{
        display: flex;
        max-width: 700px;
        margin: 0 auto;
        overflow: hidden;
        p{
            min-width: 700px;
            font-style: italic;
            font-size: 22px;
            margin: 0 auto;
            line-height: 32px;
        }
    }
}

.content{
    color: $color-resp-text;
    .content-cards-cover{
        padding: 50px 0;
        display: flex;
        justify-content: space-between;
        .content-cards-item{
            max-width: 300px;
            h3{
                font-size: 16px;
                margin: 0 0 26px 0;
            }
            &-img-wrap{
                border: 1px solid #c8c8c8;
                width: 300px;
                height: 190px;
                box-shadow: 0px 22px 15px -19px rgba(0,0,0,0.57);
                -webkit-box-shadow: 0px 22px 15px -19px rgba(0,0,0,0.57);
                -moz-box-shadow: 0px 22px 15px -19px rgba(0,0,0,0.57);
            }
            &-img-cover{
                padding: 5px;
                width: 290px;
                height: 180px;
                img{
                    object-fit: cover;
                    width: 100%;
                    height: 100%;
                }
            }
            p{
                margin: 30px 0 25px 0;
                color: #807d77;
                font-size: 14px;
                line-height: 19px;
            }
            a{
                text-decoration: none;
                font-size: 14px;
                color: $color-social;
                font-weight: bold;
                transition: all ease-in-out 0.2s;
                &:hover{
                    color: $color-nav-text;
                }
            }
            .content-cards-item-small{
                p{
                    margin: -6px 0 0 0;;
                    color: #807d77;
                    font-size: 14px;
                    line-height: 19px;
                }
                &-img{
                    float: left;
                    margin: 0 15px 0 0;
                    height: 48px;
                    width: 48px;
                    img{
                        object-fit: cover;
                        width: 100%;
                        height: 100%;
                    }
                }
                a{
                    text-align: right;
                    display: block;
                    margin-top: 20px;
                }
                &:not(:first-child){
                    margin-top: 35px;
                }
            }
        }
    }
}

//footer settings
footer{
    background-color: #eee;
    .footer-quarters{
        display: flex;
        justify-content: space-between;
        padding: 30px 0 40px 0;
        &-item{
            h4{
                color: #505050;
                padding: 0;
                margin: 0 0 30px 0;
                font-weight: normal;
            }
            &:first-child{
                max-width: 450px;
            }
            &-small{
                display: flex;
                &:not(:first-child){
                    margin-top: 30px;
                }
                .footer-quarters-item-small-content{
                    h5{
                        color: #505050;
                        margin: 10px 0 20px 0;
                        font-size: 16px;
                    }
                    p{
                        color: #737373;
                        margin: 0;
                        font-size: 14px;
                    }
                }               
            }
            .footer-quarters-item-small-img{
                width: 107px;
                height: 105px;
                border: 1px solid #c8c8c8;
                padding: 5px;
                box-sizing: border-box;
                margin-right: 20px;
                img{
                    object-fit: cover;
                    width: 100%;
                    height: 100%;
                }
            }
            .footer-quarters-item-gallery-cover{
                display: flex;
                flex-wrap: wrap;
                justify-content: space-between;
            }
            .footer-quarters-item-gallery-cover>div{
                margin-bottom: 15px;
                height: 65px;
                width: 65px;
                img{
                    object-fit: cover;
                    width: 100%;
                    height: 100%;
                }
            }
        }
    }
    hr{
        margin: 0;
        padding: 0;
        border: 1px solid #e0e0e0;
    }
    .footer-copyright{
        display: flex;
        justify-content: space-between;
        padding: 25px 0;
        color: #737373;
        font-size: 14px;
    }
    .twit{
        max-width: 225px;
        p{
            color: #727272;
        }
        span{
            color: #2d3f51;
        }
    }
    .gallery{
        max-width: 225px;
    }
}

