//keyframes
@keyframes slide{
    0%{
        transform: translateX(2000px);
    }
    100%{
        transform: translateX(0px);
    }
}

@keyframes slide-but{
    0%{
        transform: translateX(-2000px);
    }
    100%{
        transform: translateX(0px);
    }
}

@keyframes preloader{
    from{
        transform: rotate(0deg);
    }
    to{
        transform: rotate(360deg);
    }
}